<template>
  <div class="main-box growth">
    <div class="main">
      <div class="middle">
        <div class="left">
          <el-map
            @mapObiterData="mapObiterData"
            @generalData="generalData"
            :tagInfoData="tagInfoData"
            ref="map"
            v-loading="loading"
            element-loading-background="rgba(0, 0, 0, 0)"
          ></el-map>
        </div>
        <div class="right">
          <p class="refresh">数据每隔五分钟自动刷新<button class="btn1" @click="refresh"><i class="iconfont">&#xe667;</i>刷新</button></p>
          <ul class="ranking">
            <li>
              <h3 class="title"><em></em>以平均身高高低排名</h3>
              <div class="content">
                <el-line
                  :xData="statureRankings.xData"
                  :data="statureRankings.data"
                  :color="statureRankings.color"
                  :yName="statureRankings.yName"
                  v-if="statureRankings.data[0].value.findIndex(v => v !== 0) !== -1 && statureRankings.data[1].value.findIndex(v => v !== 0) !== -1"
                ></el-line>
                <no-data v-else :item="'平均身高'" />
              </div>
            </li>
            <li>
              <h3 class="title"><em></em>以平均体重高低排名</h3>
              <div class="content">
                <bars
                  :xData="weightRankings.xData"
                  :data="weightRankings.data"
                  :color="weightRankings.color"
                  :yName="weightRankings.yName"
                  :grid="weightRankings.grid"
                  v-if="weightRankings.data[0].value.findIndex(v => v !== 0) !== -1 && weightRankings.data[1].value.findIndex(v => v !== 0) !== -1"
                ></bars>
                <no-data v-else :item="'平均体重'" />
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="line"></div>
      <div class="bottom">
        <ul class="statistics">
          <li>
            <h3 class="title"><em></em>女童/男童BMI分布</h3>
            <ul class="content">
              <li>
                <pie
                  :data="girlData.data"
                  :color="girlData.color"
                  :title="girlData.title"
                  v-if="girlData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'女童BMI'" />
              </li>
              <li>
                <pie
                  :data="boyData.data"
                  :color="boyData.color"
                  :title="boyData.title"
                  v-if="boyData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'男童BMI'" />
              </li>
            </ul>
          </li>
          <li></li>
          <li>
            <h3 class="title"><em></em>幼儿园信息</h3>
            <div class="content">
              <matts
                :data="KindergartenData"
              ></matts>
            </div>
          </li>
          <li>
            <h3 class="title"><em></em>教师信息</h3>
            <ul class="content">
              <li>
                <pie
                  :data="jobData.data"
                  :color="jobData.color"
                  :title="jobData.title"
                  :pieSize="jobData.pieSize"
                  v-if="jobData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'职称'" />
              </li>
              <li>
                <pie
                  :data="ageData.data"
                  :color="ageData.color"
                  :title="ageData.title"
                  :pieSize="ageData.pieSize"
                  v-if="ageData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'年龄'" />
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import ElMap from '@/common/echarts/Map'
import Matts from '@/pages/home/healthy/today/components/Matts'
import Pie from '@/common/echarts/Pie'
import ElLine from '@/common/echarts/Line'
import Bars from '@/common/echarts/expand/Bars'
export default {
  name: 'HealthyToday',
  components: {
    ElMap,
    Matts,
    Pie,
    ElLine,
    Bars
  },
  data () {
    return {
      loading: true,
      // 地图标签数据
      tagInfoData: {
        name: '身体发育',
        data: []
      },
      // 以平均身高高低排名
      statureRankings: {
        xData: ['暂无数据'],
        data:
        [
          {
            name: '男童',
            value: [0]
          },
          {
            name: '女童',
            value: [0]
          }
        ],
        color: ['#53e9ee', '#ffe98f'],
        yName: '身高（cm）'
      },
      // 以平均身高高低排名
      weightRankings: {
        xData: ['暂无数据'],
        data:
        [
          {
            name: '男童',
            value: [0]
          },
          {
            name: '女童',
            value: [0]
          }
        ],
        yName: '体重（kg）',
        color: ['#ff6dff', '#315bff'],
        grid: {
          top: '20%',
          left: '0%',
          right: '0%',
          bottom: '10%',
          containLabel: true
        }
      },
      // 各年级男童/女童BMI分布-女童
      girlData: {
        data: [
          {value: 0, name: '偏轻'},
          {value: 0, name: '均值'},
          {value: 0, name: '肥胖'},
          {value: 0, name: '超重'}
        ],
        color: ['#ff6c6c', '#ff9786', '#ffd05a', '#ffe98f', '#9d70ff'],
        title: '女童'
      },
      // 各年级男童/女童BMI分布-男童
      boyData: {
        data: [
          {value: 0, name: '偏轻'},
          {value: 0, name: '均值'},
          {value: 0, name: '肥胖'},
          {value: 0, name: '超重'}
        ],
        color: ['#315bff', '#7193ff', '#53e9ee', '#a4ffee', '#24cbff'],
        title: '男童'
      },
      // 幼儿园信息
      KindergartenData: [
        {
          unit: '家',
          subText: '园所总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '园长总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在职教师总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在园儿童总数',
          sum: 0
        }
      ],
      // 职称分布
      jobData: {
        data: [
          {value: 0, name: '正高级'},
          {value: 0, name: '高级'},
          {value: 0, name: '一级'},
          {value: 0, name: '二级'},
          {value: 0, name: '三级'}
        ],
        color: ['#ffbc00', '#57d0ff', '#4d6bff', '#ff6562', '#9d70ff'],
        title: '职称分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      },
      // 年龄分布
      ageData: {
        data: [
          {value: 0, name: '00后'},
          {value: 0, name: '90后'},
          {value: 0, name: '80后'},
          {value: 0, name: '70后'}
        ],
        color: ['#1683fb', '#61ffe1', '#24cbff', '#7189ff'],
        title: '年龄分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      }
    }
  },
  methods: {
    // 平均身高，平均体重, BMI
    mapObiterData (params) {
      let time = new Date()
      let data = {}
      const StartDate = time.getFullYear() + '-' + (time.getMonth() + 1) + '-01' + ' 00:00:01'
      const EndDate = time.getFullYear() + '-' + (time.getMonth() + 1) + '-' + time.getDate() + ' 23:59:59'
      const idObj = this.getAreaId(params.name)
      switch (params.level) {
        case '省级':
          data = {ProvinceId: idObj[0].ProvinceId, CityId: 0, DistrictId: 0, Gid: 0, StartDate: StartDate, EndDate: EndDate}
          break
        case '市级':
          data = {ProvinceId: idObj[0].ProvinceId, CityId: idObj[0].CityId, DistrictId: 0, Gid: 0, StartDate: StartDate, EndDate: EndDate}
          break
        case '县级':
          data = {ProvinceId: idObj[0].ProvinceId, CityId: idObj[0].CityId, DistrictId: idObj[0].DistrictId, Gid: 0, StartDate: StartDate, EndDate: EndDate}
          break
      }
      // 身高体重
      const avgData = data
      // const avgData = {ProvinceId: idObj[0].ProvinceId, CityId: 0, DistrictId: 0, Gid: 0, StartDate: '2018-04-10 00:00:01', EndDate: '2018-04-10 23:59:59'}
      avgData.IsMonth = false
      window.$.ajax({
        url: 'http://api.youjiaoyun.net/weixin.asmx/GetAvgBodyCheckResult?jsoncallback=?',
        dataType: 'jsonp',
        data: avgData,
        jsonp: 'jsoncallback',
        async: true,
        success: (res) => {
          window.$('.refresh').find('i').removeClass('loading')
          this.loading = false
          const list = this.$store.state.mapArea.list
          let result = {
            heightData: {
              girl: [],
              boy: []
            },
            weightData: {
              girl: [],
              boy: []
            },
            height: [],
            weight: []
          }
          for (let i = 0, len = list.length; i < len; i++) {
            result.height.push({
              city: list[i],
              girl: 0,
              boy: 0
            })
            result.weight.push({
              city: list[i],
              girl: 0,
              boy: 0
            })
            const res0 = res.filter((item) => {
              return item.AreaName === list[i] && item.Gender === 0
            })
            const res1 = res.filter((item) => {
              return item.AreaName === list[i] && item.Gender === 1
            })
            // 女童
            if (res0[0]) {
              let Hsum = 0
              let Wsum = 0
              for (let i = 0, len = res0.length; i < len; i++) {
                Hsum += res0[i].AvgHeight
                Wsum += res0[i].AvgWeight
              }
              Hsum /= res0.length
              Wsum /= res0.length
              result.height[i].girl = Hsum
              result.weight[i].girl = Wsum
            } else {
              result.height[i].girl = 0
              result.weight[i].girl = 0
            }
            // 男童
            if (res1[0]) {
              let Hsum = 0
              let Wsum = 0
              for (let i = 0, len = res1.length; i < len; i++) {
                Hsum += res1[i].AvgHeight
                Wsum += res1[i].AvgWeight
              }
              Hsum /= res1.length
              Wsum /= res1.length
              result.height[i].boy = Hsum
              result.weight[i].boy = Wsum
            } else {
              result.height[i].boy = 0
              result.weight[i].boy = 0
            }
          }
          // 以平均身高高低排名
          result.height.sort((a, b) => {
            return (b.girl + b.boy) - (a.girl + a.boy)
          })
          let xDataList = []
          for (let i = 0, len = result.height.length; i < len; i++) {
            xDataList.push(result.height[i].city)
            result.heightData.girl.push(result.height[i].girl)
            result.heightData.boy.push(result.height[i].boy)
          }
          this.statureRankings = {
            xData: xDataList,
            data:
            [
              {
                name: '男童',
                value: result.heightData.boy
              },
              {
                name: '女童',
                value: result.heightData.girl
              }
            ],
            color: ['#53e9ee', '#ffe98f'],
            yName: '身高（cm）'
          }
          // 以平均体重高低排名
          result.weight.sort((a, b) => {
            return (b.girl + b.boy) - (a.girl + a.boy)
          })
          xDataList = []
          for (let i = 0, len = result.weight.length; i < len; i++) {
            xDataList.push(result.weight[i].city)
            result.weightData.girl.push(result.weight[i].girl)
            result.weightData.boy.push(result.weight[i].boy)
          }
          this.weightRankings = {
            xData: xDataList,
            data:
            [
              {
                name: '男童',
                value: result.weightData.boy
              },
              {
                name: '女童',
                value: result.weightData.girl
              }
            ],
            yName: '体重（kg）',
            color: ['#ff6dff', '#315bff'],
            grid: {
              top: '20%',
              left: '0%',
              right: '0%',
              bottom: '10%',
              containLabel: true
            }
          }
        },
        error: function (err) {
          console.log('err' + err)
        }
      })
      // BMI
      window.$.ajax({
        url: 'http://api.youjiaoyun.net/weixin.asmx/GetBodyCheckResultList?jsoncallback=?',
        dataType: 'jsonp',
        data: data,
        jsonp: 'jsoncallback',
        async: true,
        success: (res) => {
          // 女童
          this.girlData.data = this.avgHW(res, 0)
          // 男童
          this.boyData.data = this.avgHW(res, 1)
          this.forTag(res)
        },
        error: function (err) {
          console.log(err)
        }
      })
    },
    avgHW (res, sex) {
      let arr = []
      let BMI = []
      if (this.$store.state.mapArea.level === '省级') {
        BMI = res.filter((item) => {
          return item.Gender === sex
        })
      } else {
        let name = ''
        switch (this.$store.state.mapArea.level) {
          case '市级':
            name = 'CityName'
            break
          case '县级':
            name = 'DistrictName'
            break
        }
        const areaName = this.$store.state.mapArea.name
        BMI = res.filter((item) => {
          return item.Gender === sex && item[name] === areaName
        })
      }
      BMI = this.$common.compare(BMI, 'BMIResult')
      if (Object.keys(BMI).length !== 0) {
        const defaultVal = {
          '偏轻': 0,
          '均值': 0,
          '肥胖': 0,
          '偏重': 0,
          '超重': 0
        }
        for (let key in defaultVal) {
          BMI[key] ? defaultVal[key] = BMI[key] : defaultVal[key] = 0
        }
        window.$.each(defaultVal, (key, value) => {
          arr.push({
            name: key,
            value: value
          })
        })
      } else {
        arr = [{name: '暂无数据', value: 0}]
      }
      return arr
    },
    forTag (res) {
      // 地图标签信息
      let name = ''
      switch (this.$store.state.mapArea.level) {
        case '省级':
          name = 'CityName'
          break
        case '市级':
          name = 'DistrictName'
          break
      }
      const arr = []
      for (let i = 0, len = this.$store.state.mapArea.list.length; i < len; i++) {
        let defaultVal = {
          '偏轻': 0,
          '均值': 0,
          '肥胖': 0,
          '偏重': 0,
          '超重': 0
        }
        let BMI = res.filter((item) => {
          return item[name] === this.$store.state.mapArea.list[i]
        })
        BMI = this.$common.compare(BMI, 'BMIResult')
        for (let key in defaultVal) {
          BMI[key] ? defaultVal[key] = BMI[key] : defaultVal[key] = 0
        }
        arr.push({
          name: this.$store.state.mapArea.list[i],
          data: defaultVal
        })
      }
      this.tagInfoData.data = arr
    },
    // 获取该地区id
    getAreaId (name) {
      return this.$store.state.areaIdList.filter((item) => {
        return item.ProvinceName === name || item.CityName === name || item.DistrictName === name
      })
    },
    // 刷新
    refresh () {
      window.$('.refresh').find('i').addClass('loading')
      this.loading = true
      console.log(this.$store.state.mapArea.level)
      const obj = {
        level: this.$store.state.mapArea.level,
        name: this.$store.state.mapArea.name
      }
      this.mapObiterData(obj)
    },
    // 幼儿园，教师信息
    generalData (d) {
      this.KindergartenData[0].sum = d.gardenCount
      this.KindergartenData[1].sum = d.kindergartenCount
      this.KindergartenData[2].sum = d.teacherCount
      this.KindergartenData[3].sum = d.totalCount
      const ages = ['00', '90', '80', '70']
      const jobs = ['正高级', '高级', '一级', '二级', '三级']
      this.jobData.data = [
        {value: 0, name: '正高级'},
        {value: 0, name: '高级'},
        {value: 0, name: '一级'},
        {value: 0, name: '二级'},
        {value: 0, name: '三级'}
      ]
      this.ageData.data = [
        {value: 0, name: '00后'},
        {value: 0, name: '90后'},
        {value: 0, name: '80后'},
        {value: 0, name: '70后'}
      ]
      if (d.jobs.length) {
        for (let i = 0, len = jobs.length; i < len; i++) {
          const result = d.jobs.filter((item) => {
            return item.item1 === jobs[i]
          })
          result.length ? this.jobData.data[i].value = result[0].item2 : this.jobData.data[i].value = 0
        }
      } else {
        this.jobData.data = [{name: '暂无数据', value: 0}]
      }
      if (d.ages.length) {
        for (let i = 0, len = ages.length; i < len; i++) {
          const result = d.ages.filter((item) => {
            return item.item1.substring(0, 1) === ages[i].substring(0, 1)
          })
          result.length ? this.ageData.data[i].value = result.length : this.ageData.data[i].value = 0
        }
      } else {
        this.ageData.data = [{name: '暂无数据', value: 0}]
      }
    },
    // 当前县所有园区列表
    gardenList: function () {
    // gardenList: async function (areaName) {
      // const res = await http.get(api.province, {
      //   provinceName: areaName
      // })
      // if (res.data) {
      //   return res.data.data
      // }
      return [{
        name: '宝塔区双睿幼儿园',
        gardenId: 1
      }]
    },
    tagInfo (d) {
      const defaultTagInfo = {
        warnTempCount: 0,
        jcCount: 0,
        fyCount: 0,
        gcCount: 0,
        attendCount: 0
      }
      for (let i in d) {
        d[i] = window.$.extend({}, defaultTagInfo, d[i])
      }
      console.log('当前区域所有下级总览信息')
      console.log(d)
    }
  },
  mounted () {
    // 初始化所有边框样式
    this.$common.border(window.$('.bottom .content'))
  }
}
</script>

<style lang="stylus" scoped>
.growth
  .main
    .bottom
      .statistics > li:nth-child(1)
        flex: 0 0 50%
        .content
          li
            float: left
            width: 50%
            height: 100%
            padding: 20px 10px
            box-sizing: border-box
      .statistics > li:nth-child(2)
        display: none
</style>



